import Datepicker from 'vuejs-datepicker'
import {bg, en} from 'vuejs-datepicker/dist/locale/index'

export default {
  components: {
    Datepicker,
  },
  data() {
    return {
      formData: {
        fromDate: null,
        toDate: null,
        itemsPerPage: 5,
      },
      locales: {
        en: en,
        bg: bg,
      },
      optionsPerPage: [
        { value: 2, text: '2' },
        { value: 5, text: '5' },
        { value: 10, text: '10' },
        { value: 25, text: '25' },
        { value: 50, text: '50' },
      ],
    }
  },
  methods: {
    sendDates() {
      let output = {
        fromDate: this.formData.fromDate,
        toDate: this.formData.toDate,
        itemsPerPage: this.formData.itemsPerPage,
      }
      if (output.fromDate <= output.toDate || output.toDate === null) {
        this.$emit('datesChanged', output)
      } else {
        this.$customMsg.showErrorMessage(this.$t('End date is before start date'))
      }
    },
    openRPModal() {
      this.$emit('openRPModal')
    },
  },
}



